//Section 1
.section_first {
  background-color: white;
  position: relative;
  padding-top: 50px;
  border-bottom: 1px solid $yellow;
  border-top: 1px solid $yellow;

  &::before {
    position: absolute;
    content: "";
    display: inline-block;
    height: 1vh;
    width: 100%;
    background-color: white;
    top: -2.5vh;
    border-bottom: 1px solid $yellow;
    border-top: 1px solid $yellow;
  }

  &::after {
    position: absolute;
    content: "";
    display: inline-block;
    height: 1vh;
    width: 100%;
    background-color: white;
    bottom: -2.5vh;
    border-bottom: 1px solid $yellow;
    border-top: 1px solid $yellow;
  }
}

.sf_header {
  font-family: 'Damion', cursive;
  font-weight: lighter;
  text-align: center;
  font-size: 3.5em;
  color: $pink;
  transition: 1s;
  opacity: 0;
  transform: translateY(-50px);
}

.sf_container {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 150px;
  overflow: hidden;
  margin-bottom: -50px;
}

.sf_pic {
  height: 100%;
  width: 300px;
  margin-top: 50px;
  background-color: pink;
  background-image: url("../img/cupCakes.jpg");
  background-size: contain;
  opacity: 0;
  transform: translateX(-300px);
  border: 2px solid rgb(255, 0, 84);

}

.sf_textblock {
  height: auto;
  width: 300px;
  opacity: 0;
  text-align: justify;
  margin-top: 50px;
  padding-left: 10px;
  padding-right: 10px;
  transform: translateX(300px);
  h2 {
    color: $pink;
    text-align: center;
    margin-bottom: 30px;
    border-bottom: 2px dotted $pink;
    padding-bottom: 30px;
  }
}

@media (min-width: 800px) {
  .section_first {
    padding-top: 100px;
  }

  .sf_container {
    margin-bottom: 0px;
  }

  .sf_header {
    margin-bottom: 50px;
  }

  .sf_textblock {
    width: 500px;
  }
  .sf_pic {
    height: 360px;
    width: auto;
  }
}
@media (min-width: 1300px) {
  .sf_textblock {
    margin-top: 0px;
  }
}



//                     LANDING ANIMATIONS

.sfh_land {
  @include land(1s, translateY(0px));
}

.sft_land {
  @include land(1s, translateX(0px));
}
