.section_third {
  height: auto;
  position: relative;
  border-top: 1px solid $yellow;
  border-bottom: 1px solid $yellow;
  background-color: white;
  // margin-bottom: 150px;
  padding-bottom: 100px;
  overflow: auto;
}

.st_header {
  font-family: 'Damion', cursive;
  font-weight: lighter;
  text-align: center;
  font-size: 3.5em;
  color: $pink;
  margin: 50px auto;
  transition: 1s;
  opacity: 0;
  transform: translateY(-50px);
}

.st_container {
  margin: 0 auto;
  width: 100%;
  max-width: 1000px;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
}

.st_pic {
  position: relative;
  height: 200px;
  width: 300px;
  margin: 10px;
  border: 1px solid $pink;
  background-color: $yellow;
  opacity: 0;
  transform: scale(1.05);
  transition: 0.5s;
  overflow: hidden;
  background-image: url("../img/cupCakes.jpg");
  background-size: contain;
  cursor: pointer;

  div {
    background-color: $pinkglass;
    height: 100%;
    width: 100%;
    position: absolute;
  }
  div::before {
    box-sizing: border-box;
    content: "";
    position: absolute;
    height: 200px;
    width: 300px;
    left: -100%;
    background-color: transparent;
    border-bottom: 100px solid transparent;
    border-top: 100px solid $pinkglass;
    border-right: 150px solid $pinkglass;
    border-left: 150px solid transparent;
  }
}

  .no-touch .st_pic:hover div{
    right: 0;
    transition: 0.5s;
    }


  .st_picdiv_inactive div {
    right: -200%;
    transition: 0.5s;
  }
  .st_picdiv_active div {
    right: 0;
    transition: 0.5s;
  }




//                     LANDING ANIMATIONS

.sth_land {
  @include land(1s, translateY(0px));
}
.stp_land {
  @include land(1s, scale(1));
}
